*{
    padding: 0;
    margin: 0;
}
.box{
    width: 300px;
    height: 450px;
    padding: 0 20px;
    border: 2px solid #1688ea;
    margin: 50px auto;
}
.tit{
    margin-top: 5px;
    margin-bottom: 30px;
}
.brand{
    color: #1688ea;
    font-size: 18px;
    margin-right: 20px;
}
.inp{
    width: 100%;
    height: 40px;
    text-indent: 8px;
}
.notice{
    height: 30px;
    line-height: 30px;
    font-size: 12px;
}

.code{
    width: 60%;
}
.getCode{
    width: 35%;
    height: 44px;
    float: right;
    border: none;
    background-color: #eee;
}
.codeImg{
    height: 40px;
}
.btn{
    background: #1688ea;
    width: 100%;
    height: 40px;
    border: none;
    margin-top: 8px;
    color: #fff;
    border-radius: 3px;
}