*{
    padding: 0;
    margin: 0;
}
a{
    text-decoration: none; 
}
header{
    height: 50px;
    line-height: 48px;
    background-color: #20232a; 
}
nav{
    width: 800px;
    position: relative;
    left: 200px;
    top: -48px;

}
header a{
    display: inline-block;
    height: 48px;
    padding: 0 15px;
    color: #fff; 
}
header .active{
    border-bottom: 2px solid rgb(97, 218, 251);
    color: rgb(97, 218, 251);
}
text{
    font-weight: bold;
    color: aqua;
    font-size: large;
    margin-left: 20px;
}